// ‚úÖ ProductSelectModal.vue (ÏµúÏ¢Ö ÏôÑÏÑ± Î≤ÑÏ†Ñ)
<template>
  <div class="modal fade show d-block" style="background: rgba(0,0,0,0.5); z-index: 1051;" @click.self="$emit('close')">
    <div class="modal-dialog modal-lg">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">Ï†úÌíà ÏÑ†ÌÉù</h5>
          <button type="button" class="btn-close" @click="$emit('close')"></button>
        </div>
        <div class="modal-body">
          <div class="d-flex justify-content-between align-items-center mb-2">
            <input
              type="text"
              class="form-control w-25"
              placeholder="Ï†úÌíàÎ™Ö Í≤ÄÏÉâ"
              v-model="search" />
          </div>

          <table class="table table-sm table-bordered text-center">
            <thead class="table-light">
              <tr>
                <th>Ï†úÌíàID</th>
                <th>Ï†úÌíàÎ™Ö</th>
              </tr>
            </thead>
            <tbody>
              <tr
              v-for="item in filteredProducts"
              :key="item.prd_no"
              @click="selectProduct(item)" 
              style="cursor: pointer;">
              <td>{{ item.prd_no }}</td>
              <td>{{ item.prd_nm }}</td>              
            </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // Î∂ÄÎ™®Î°úÎ∂ÄÌÑ∞ Ï†ÑÎã¨Î∞õÎäî props (ÏÜçÏÑ±Í∞í)
  props: {
    prodList: {
    type: Array,
    default: () => []
  }
},
  // Ïª¥Ìè¨ÎÑåÌä∏ ÎÇ¥Î∂ÄÏóêÏÑú ÏÇ¨Ïö©ÌïòÎäî Îç∞Ïù¥ÌÑ∞ Ï†ïÏùò
  data() {
    return {
      search: '',         // Í≤ÄÏÉâÏñ¥ ÏûÖÎ†•Í∞í
      // selectedProd: []    // ÏÇ¨Ïö©ÏûêÍ∞Ä ÏÑ†ÌÉùÌïú Ï†úÌíà Î™©Î°ù (Ï≤¥ÌÅ¨ ÏÉÅÌÉú Ïú†ÏßÄ)
    };
  },

  // Í≥ÑÏÇ∞Îêú ÏÜçÏÑ± (ÌôîÎ©¥Ïóê Î∞òÏùëÌòïÏúºÎ°ú ÏûêÎèô Í∞±Ïã†Îê®)
  computed: {
    // Í≤ÄÏÉâÏñ¥Î•º Í∏∞Ï§ÄÏúºÎ°ú prodListÎ•º ÌïÑÌÑ∞ÎßÅÌïú Í≤∞Í≥º Î∞òÌôò
    filteredProducts() {
      if (!this.prodList) return []  // prodListÍ∞Ä ÏóÜÏúºÎ©¥ Îπà Î∞∞Ïó¥ Î∞òÌôò
      return this.prodList.filter(p => p.prd_nm?.includes(this.search))  // Ï†úÌíàÎ™Ö Ìè¨Ìï® Ïó¨Î∂ÄÎ°ú ÌïÑÌÑ∞ÎßÅ
    }
  },
  
  // ÏÇ¨Ïö©Ïûê ÎèôÏûëÏóê Îî∞Î•∏ Î©îÏÑúÎìú Ï†ïÏùò
  methods: {

    selectProduct(item){
      this.$emit('select-product', item);
      this.$emit('close');
    },
  }
}
</script>

<style scoped>
.table-primary {
  background-color: #cce5ff;
}
