<template>
  <div class="container mt-4">
    <div class="d-flex justify-content-between align-items-center mb-4">
      <div class="d-flex gap-4">
        <h2 class="mb-4">ÏôÑÏ†úÌíàÍ≤ÄÏÇ¨Í∏∞Ï§ÄÏÑú</h2>
      </div>
    </div>
    <!-- Ï°∞Ìöå Ï°∞Í±¥ -->
    <div class="input">
      Ï†úÌíàÎ≤àÌò∏ <input v-model="searchQuery" class="form-control" id="input_id" placeholder="" readonly />
      <button class="btn btn-outline-secondary" id="icon-btn" @click="openProductModal">üîç</button>
      Ï†úÌíàÎ™Ö <input :value="selectedProductName" class="form-control" id="input" readonly
        style="background-color: #eee;" />
    </div>
    <br>

    <!-- ÌÖåÏù¥Î∏î -->
    <!-- ÌÖåÏù¥Î∏î Ìó§Îçî -->
    <table class="table table-bordered text-center ">
      <thead class="table-light">
        <tr>
          <th>NO</th>
          <th>Í≤ÄÏÇ¨Ìï≠Î™©</th>
          <th>Í≤ÄÏÇ¨Í∏∞Ï§Ä</th>
          <th>Í∑úÍ≤©</th>
          <th>ÏÇ¨Ïö©Ïû•ÎπÑ</th>
          <th>ÏûëÏÑ±Ïûê</th>
          <th>ÏàòÏ†ïÏùºÏûê</th>
          <th>ÎπÑÍ≥†</th>
          <th>ÏàòÏ†ï</th>
        </tr>
      </thead>

      <!-- ÌÖåÏù¥Î∏î Î∞îÎîî -->
      <tbody>
        <!-- Í∏∞Ï§ÄÏÑú Ìï≠Î™© Ï∂îÍ∞Ä Ïãú ÌÖåÏù¥Î∏î ÏûÖÎ†•Ïπ∏ -->
        <tr>
          <td></td>
          <td><input v-model="newItem.ins_itm" class="form-control" placeholder="Í≤ÄÏÇ¨Ìï≠Î™©" /></td>
          <td><input v-model="newItem.ins_mthd" class="form-control" placeholder="Í≤ÄÏÇ¨Í∏∞Ï§Ä" /></td>
          <td><input v-model="newItem.ins_spc" class="form-control" placeholder="Í∑úÍ≤©" /></td>
          <td><input v-model="newItem.ins_eqp" class="form-control" placeholder="ÏÇ¨Ïö©Ïû•ÎπÑ" /></td>
          <td><input v-model="newItem.crt_by" class="form-control" placeholder="ÏûëÏÑ±Ïûê" readonly
              style="background-color: #eee;" />
          </td>
          <td><input v-model="newItem.mdf_dt" class="form-control" placeholder="ÏàòÏ†ïÏùºÏûê" readonly
              style="background-color: #eee;" /></td>
          <td><input v-model="newItem.rmk" class="form-control" placeholder="ÎπÑÍ≥†" /></td>
          <td>
            <button @click="spmInsStdInsert" class="btn btn-primary btn-sm">Ï∂îÍ∞Ä</button>
          </td>
        </tr>
        <tr v-for="(item, index) in spmInsStdList" :key="item.spm_ins_std_no">
          <td>
            {{ index + 1 }}
          </td>
          <td>
            <template v-if="item.editMode">
              <input v-model="item.ins_itm" class="form-control" />
            </template>
            <template v-else>
              {{ item.ins_itm }}
            </template>
          </td>
          <td>
            <template v-if="item.editMode">
              <input v-model="item.ins_mthd" class="form-control" />
            </template>
            <template v-else>
              {{ item.ins_mthd }}
            </template>
          </td>
          <td>
            <template v-if="item.editMode">
              <input v-model="item.ins_spc" class="form-control" />
            </template>
            <template v-else>
              {{ item.ins_spc }}
            </template>
          </td>
          <td>
            <template v-if="item.editMode">
              <input v-model="item.ins_eqp" class="form-control" />
            </template>
            <template v-else>
              {{ item.ins_eqp }}
            </template>
          </td>
          <td>
            <template v-if="item.editMode">
              <input v-model="item.crt_by" class="form-control" readonly style="background-color: #eee;" />
            </template>
            <template v-else>
              {{ item.crt_by }}
            </template>
          </td>
          <td>
            <template v-if="item.editMode">
              <input v-model="item.mdf_dt" class="form-control" readonly style="background-color: #eee;"/>
            </template>
            <template v-else>
              {{ dateFormat(item.mdf_dt, 'yyyy-MM-dd') }}
            </template>
          </td>
          <td>
            <template v-if="item.editMode">
              <input v-model="item.rmk" class="form-control" />
            </template>
            <template v-else>
              {{ item.rmk }}
            </template>
          </td>
          <td>
            <button v-if="!item.editMode" @click="enableEditMode(index)" class="btn btn-warning btn-sm">ÏàòÏ†ï</button>
            <button v-if="item.editMode" @click="saveRow(index)" class="btn btn-success btn-sm">Ï†ÄÏû•</button>
            <button v-if="item.editMode" @click="disableEditMode(index)" class="btn btn-secondary btn-sm">Ï∑®ÏÜå</button>
            <button @click="deleteRow(index)" class="btn btn-danger btn-sm">ÏÇ≠Ï†ú</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>

  <!-- Ï†úÌíà Í≤ÄÏÉâ Î™®Îã¨ -->
  <PrdSelModal v-if="showProductModal" :prodList="prodList" @select-product="handleSelectedProduct"
    @close="showProductModal = false" />
</template>

<script>
import axios from 'axios';
import useDateUtils from '@/utils/useDates.js' // ÎÇ†Ïßú Ìè¨Îß∑ Ïú†Ìã∏
import PrdSelModal from '@/views/qualitys/PrdSelModal.vue'; // Î™®Îã¨

import { ref, onBeforeMount } from 'vue';

// Î∞òÏùëÌòï Í∞ùÏ≤¥ ÏÑ†Ïñ∏ : ÏõêÏãúÌÉÄÏûÖ    
const isUpdated = ref(false);

export default {
  components: { PrdSelModal },
  data() {
    return {
      searchQuery: '',             // Ï†úÌíà ID
      selectedProductName: '',     // Ï†úÌíàÎ™Ö
      spmInsStdList: [],
      prodList: [],
      showProductModal: false,
      newItem: {
        ins_itm: '',
        ins_mthd: '',
        ins_spc: '',
        ins_eqp: '',
        rmk: '',
        crt_by: '',
        mdf_dt: '',
      },
      // updateItem: {
      //   ins_itm: '',
      //   ins_mthd: '',
      //   ins_spc: '',
      //   ins_eqp: '',
      //   rmk: '',
      //   spm_ins_std_no: '',
      // },
    };
  },
  methods: {
    // Îì±Î°ù
    async spmInsStdInsert() {
      if (!this.searchQuery) {
        alert('Ï†úÌíàÏùÑ Î®ºÏ†Ä ÏÑ†ÌÉùÌïòÏÑ∏Ïöî.');
        return;
      }

      let obj = {
        ...this.newItem,
        prd_no: this.searchQuery, // ÏÑ†ÌÉùÎêú Ï†úÌíà Î≤àÌò∏
        crt_by: this.selectedCrtBy, // ÏûëÏÑ±Ïûê
        rgt_dt: new Date(), // Îì±Î°ùÏùºÏûê
        mdf_dt: new Date(), // ÏàòÏ†ïÏùºÏûê
      };

      try {
        let result = await axios.post("/api/spmInsStd", obj);
        let addRes = result.data;

        if (addRes.isSuccessed) {
          alert('Îì±Î°ùÎêòÏóàÏäµÎãàÎã§.');
          this.spmInsStdList.push({ ...obj, spm_ins_std_no: addRes.prdNo }); // ÌÖåÏù¥Î∏îÏóê Ï∂îÍ∞Ä
          this.newItem = { ins_itm: '', ins_mthd: '', ins_spc: '', ins_eqp: '', rmk: '', crt_by: '', mdf_dt: '' }; // ÏûÖÎ†• ÌïÑÎìú Ï¥àÍ∏∞Ìôî
        } else {
          alert('Î™®Îì† ÌïÑÎìúÎ•º ÌôïÏù∏ÌïòÏÑ∏Ïöî.');
        }
      } catch (err) {
        alert('Îì±Î°ù Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
      }
    },

    // ÎÇ†Ïßú Ìè¨Îß∑
    dateFormat(value, format) {
      return useDateUtils.dateFormat(value, format)
    },

    // ÏàòÏ†ï ÌôúÏÑ±Ìôî
    enableEditMode(index) {
      // ÌòÑÏû¨ Îç∞Ïù¥ÌÑ∞Î•º originalDataÏóê Ï†ÄÏû•
      this.spmInsStdList[index].originalData = { ...this.spmInsStdList[index] };
      this.spmInsStdList[index].editMode = true; // ÏàòÏ†ï Î™®Îìú ÌôúÏÑ±Ìôî
    },
    // ÏàòÏ†ï ÎπÑÌôúÏÑ±Ìôî(Ï∑®ÏÜå)
    disableEditMode(index) {
      // originalDataÎ•º ÏÇ¨Ïö©ÌïòÏó¨ Îç∞Ïù¥ÌÑ∞Î•º Î≥µÏõê
      if (this.spmInsStdList[index].originalData) {
        this.spmInsStdList[index] = {
          ...this.spmInsStdList[index].originalData,
          editMode: false, // ÏàòÏ†ï Î™®Îìú ÎπÑÌôúÏÑ±Ìôî
        };
        delete this.spmInsStdList[index].originalData; // Î≥µÏõê ÌõÑ originalData ÏÇ≠Ï†ú
      } else {
        this.spmInsStdList[index].editMode = false; // ÏàòÏ†ï Î™®Îìú ÎπÑÌôúÏÑ±Ìôî
      }
    },

    // ÏàòÏ†ï
    async saveRow(index) {
      let obj = {
        ins_itm: this.spmInsStdList[index].ins_itm || '', // null Í∞í Î∞©ÏßÄ
        ins_mthd: this.spmInsStdList[index].ins_mthd || '',
        ins_spc: this.spmInsStdList[index].ins_spc || '',
        ins_eqp: this.spmInsStdList[index].ins_eqp || '',
        rmk: this.spmInsStdList[index].rmk || '',
        spm_ins_std_no: this.spmInsStdList[index].spm_ins_std_no || '', // ÏàòÏ†ïÌï† Í∏∞Ï§ÄÌï≠Î™©Î≤àÌò∏
      };

      if (!this.spmInsStdList[index].spm_ins_std_no) {
        alert('spm_ins_std_no Í∞íÏù¥ ÏóÜÏäµÎãàÎã§. Îç∞Ïù¥ÌÑ∞Î•º ÌôïÏù∏ÌïòÏÑ∏Ïöî.');
        return;
      }

      try {
        let result = await axios.put(`/api/spmInsStd/${this.spmInsStdList[index].spm_ins_std_no}`, obj);
        let updateRes = result.data;

        if (updateRes.isUpdated) {
          alert('ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§.');
          this.spmInsStdList[index].editMode = false; // ÏàòÏ†ï Î™®Îìú ÎπÑÌôúÏÑ±Ìôî
          delete this.spmInsStdList[index].originalData; // Ï†ÄÏû• ÌõÑ originalData ÏÇ≠Ï†ú
        } else {
          alert('ÏàòÏ†ïÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.\nÎç∞Ïù¥ÌÑ∞Î•º ÌôïÏù∏Ìï¥Î≥¥ÏÑ∏Ïöî.');
        }
      } catch (err) {
        alert('ÏàòÏ†ï Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
      }
    },

    // ÏÇ≠Ï†ú
    async deleteRow(index) {
      if (confirm('Ï†ïÎßê ÏÇ≠Ï†úÌïòÏãúÍ≤†ÏäµÎãàÍπå?')) {
        try {
          let result = await axios.delete(`/api/spmInsStd/${this.spmInsStdList[index].spm_ins_std_no}`);
          let deleteRes = result.data;

          if (deleteRes.isDeleted) {
            alert('ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.');
            this.spmInsStdList.splice(index, 1); // ÌÖåÏù¥Î∏îÏóêÏÑú ÏÇ≠Ï†ú
          } else {
            alert('ÏÇ≠Ï†úÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.\nÎç∞Ïù¥ÌÑ∞Î•º ÌôïÏù∏Ìï¥Î≥¥ÏÑ∏Ïöî.');
          }
        } catch (err) {
          alert('ÏÇ≠Ï†ú Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
        }
      }
    },
    // Î™®Îã¨
    showModal() {
      this.showProductModal = true; // Î™®Îã¨ Ïó¥Í∏∞
    },
    hideModal() {
      this.showProductModal = false; // Î™®Îã¨ Îã´Í∏∞
    },
    openProductModal() {
      axios.get('/api/spmInsStd/prdList') // Ï†úÌíà Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
        .then(res => {
          this.prodList = Array.isArray(res.data) ? res.data : []; // Î∞∞Ïó¥Ïù∏ÏßÄ ÌôïÏù∏ ÌõÑ ÏÑ§Ï†ï
          this.showProductModal = true; // Î™®Îã¨ Ïó¥Í∏∞
        })
      .catch(err => {
          console.error('Ï†úÌíà Î™©Î°ù Î∂àÎü¨Ïò§Í∏∞ Ïã§Ìå®', err);
          this.prodList = []; // Ïã§Ìå® Ïãú Îπà Î∞∞Ïó¥Î°ú ÏÑ§Ï†ï
        });
  },


    // Ï†úÌíà ÏÑ†ÌÉù Ïãú
    handleSelectedProduct(item) {
      this.searchQuery = item.prd_no;
      this.selectedProductName = item.prd_nm;

      this.showProductModal = false;

      this.getSpmInsStdList(item.prd_no); // Í≤ÄÏÇ¨ Í∏∞Ï§ÄÏÑú Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
    },

    // Í∏∞Ï§ÄÏÑú Î™©Î°ù Í∞ÄÏ†∏Ïò§Í∏∞
    async getSpmInsStdList(prd_no) {
      try {
        const result = await axios.get('/api/spmInsStd', { params: { prd_no } });
        this.spmInsStdList = result.data; // Í∞ÄÏ†∏Ïò® Îç∞Ïù¥ÌÑ∞Î•º ÌÖåÏù¥Î∏îÏóê Î∞îÏù∏Îî©
      } catch (err) {
        alert('Í∏∞Ï§ÄÏÑú Î™©Î°ùÏùÑ Í∞ÄÏ†∏Ïò§Îäî Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.');
      }
    }
  }
}

</script>
